import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {LoadingController} from "ionic-angular";
import {ConfigProvider} from "../config/config";


@Injectable()
export class CashByCodeServiceProvider {

  constructor(public httpClient: HttpClient,
              public configLoader: ConfigProvider,
              public loadingCtrl: LoadingController) {

  }

  //FUNCTION INIT CASH BY CODE SERVICE
  async initCashRemittance(token, userId, appId, sourceAccount, beneficiaryType, destAccount, beneficiaryName,nid, amount) {
    let initCashRemittanceServiceURL = this.configLoader.getIBankWSUrl() + "services/cashRemittance/initCashRemittance?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId + "&sourceAccount=" + sourceAccount+ "&beneficiaryType=" + beneficiaryType+ "&destAccount=" + destAccount+ "&beneficiaryName=" + beneficiaryName+"&nid=" + nid+ "&amount=" + amount;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(initCashRemittanceServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }


  //FUNCTION CONFIRM CASH BY CODE SERVICE
  async confirmCashRemittance(token, userId, appId, transferId, tpin, otp) {
    let confirmCashRemittanceServiceURL = this.configLoader.getIBankWSUrl() + "services/cashRemittance/confirmCashRemittance?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId + "&transferId=" + transferId+ "&tpin=" + tpin+ "&otp=" + otp;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(confirmCashRemittanceServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

  //FUNCTION FETCH CASH BY CODE HISTORY
  async getLocalTransferRecords(token, userId, appId, startDate, endDate) {
    let getLocalTransferRecordsServiceURL = this.configLoader.getIBankWSUrl() + "services/cashRemittance/getLocalTransferRecords?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId + "&startDate=" + startDate+ "&endDate=" + endDate;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(getLocalTransferRecordsServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

  async fetchCashByCodeBeneficiary(token, userId, appId) {
    let fetchCashByCodeBeneficiaryServiceURL = this.configLoader.getIBankWSUrl() + "services/cashRemittance/getBeneficiaryList?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(fetchCashByCodeBeneficiaryServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

  async addCashByCodeBeneficiary(token, userId, appId, beneficiaryPhoneNo, beneficiaryName, nid) {
    let addCashByCodeBeneficiaryServiceURL = this.configLoader.getIBankWSUrl() + "services/cashRemittance/addCreditAccount?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId+ "&beneficiaryPhoneNo=" + beneficiaryPhoneNo+ "&beneficiaryName=" + beneficiaryName+ "&nid=" + nid;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(addCashByCodeBeneficiaryServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

  async removeCashByCodeBeneficiary(token, userId, appId, beneficiaryPhoneNo) {
    let removeCashByCodeBeneficiaryServiceURL = this.configLoader.getIBankWSUrl() + "services/cashRemittance/removeBeneficiary?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId+ "&beneficiaryPhoneNo=" + beneficiaryPhoneNo;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(removeCashByCodeBeneficiaryServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

}



