import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {ConfigProvider} from "../config/config";
import {LoadingController} from "ionic-angular";

/*
  Generated class for the MenuServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class MenuServiceProvider {

  constructor(public httpClient: HttpClient,
              public configLoader: ConfigProvider,
              public loadingCtrl: LoadingController) {
    // console.log('Hello MenuServiceProvider Provider');
  }

  async generateMenu(userId, appId, token, appVersion) {
    let menuGenURL = this.configLoader.getIBankWSUrl()+"services/iCellAppAuth/buildUserSession?";
    let paramValue = "access_token="+token+"&userId=" + userId + "&appId=" + appId+ "&appVersion=" + appVersion;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner(),
      cssClass : 'my-class'
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(menuGenURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }
}



