import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {ConfigProvider} from "../config/config";
import {LoadingController} from "ionic-angular";
import {CryptoParamUtilProvider} from "../crypto-param-util/crypto-param-util";
import 'rxjs/add/operator/timeout';


@Injectable()
export class RemittanceProvider {


  constructor(public httpClient: HttpClient,
              public configLoader: ConfigProvider,
              public loadingCtrl: LoadingController,
              public cryptoParam : CryptoParamUtilProvider) {
  }

  ////////////FUNCTION FOR FETCHING EXCHANGE HOUSE LIST
  async getExchangeHouseList(deviceID, providedHash) {

    let headers = new HttpHeaders();
    headers = headers.append("Authorization", "Basic " + btoa("10c@t10nS3rv1c3:10c@t10nS3rv1c3P@$$"));

    // let httpParams=new HttpParams();
    // httpParams=httpParams.append("providedHash",providedHash);
    // let encryptValue = encodeURIComponent(this.exchangeHouseListReq);
    let remittanceURL = this.configLoader.getRemittanceBaseUrl() + "IBankingExHouseListAction?";
    let paramValue = "deviceID=" + deviceID+"&providedHash="+providedHash;
    // console.log(deviceID);

    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(remittanceURL +paramValue, {headers : headers}).timeout(30000).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {


        let errorResponse = {
          message: err.statusText,
          status: err.status,
          statusCode: 404
        };
        resolve(errorResponse);
        loading.dismiss();

      });
    });
  }

  ////////////FUNCTION FOR FETCHING EXCHANGE HOUSE LIST
  async searchInfoByPinNumber(deviceID, exchangeHouse, pinNumber, hash) {
    // let completeReqXML=this.searchInfoByPinNo01+pinNumber+this.searchInfoByPinNo02+exchangeHouseCode+this.searchInfoByPinNo03;
    let headers = new HttpHeaders();
    headers = headers.append("Authorization", "Basic " + btoa("10c@t10nS3rv1c3:10c@t10nS3rv1c3P@$$"));
    // let httpParams=new HttpParams();

    // httpParams=httpParams.append("deviceID", encodeURIComponent(deviceID));
    // let encryptValue = encodeURIComponent(completeReqXML);

    let remittanceURL = this.configLoader.getRemittanceBaseUrl() + "IBankingSearchFTTAction?";
    let paramValue = "deviceID=" + deviceID+"&pinNumber=" + pinNumber+"&exchangeHouse="+exchangeHouse+"&providedHash="+hash;

    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(remittanceURL + paramValue,{headers : headers}).timeout(30000).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {



        let errorResponse = {
          message: err.statusText,
          status: err.status,
          statusCode: 404
        };
        resolve(errorResponse);
        loading.dismiss();

      });
    });
  }
}



