import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {ConfigProvider} from "../config/config";
import {LoadingController} from "ionic-angular";

/*
  Generated class for the WimaxServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class WimaxServiceProvider {

  constructor(public httpClient: HttpClient,
              public configLoader: ConfigProvider,
              public loadingCtrl: LoadingController) {
    // console.log('Hello WimaxServiceProvider Provider');
  }

  async makeWimaxRecharge(token, userId, appId, provider, packageType, tPin, wimaxUser, orderDetails,  accountNo) {
    let makeWimaxRechargeServiceURL = this.configLoader.getIBankWSUrl()+"services/wimaxPayServices/confirmWimaxPay?";
    let paramValue = "access_token="+token+"&userId=" + userId + "&appId=" + appId + "&wimaxProvider=" + provider+ "&wimaxPackageType=" + packageType+ "&tpin=" + tPin+ "&wimaxAccNo=" + wimaxUser+ "&order=" + orderDetails+ "&drAccount=" + accountNo;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(makeWimaxRechargeServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

  async fetchOrderDetails(token, userId, appId, provider, packageType, accNo) {
    let orderDetailsServiceURL = this.configLoader.getIBankWSUrl()+"services/wimaxPayServices/initWimaxPayRequest?";
    let paramValue = "access_token="+token+"&userId=" + userId + "&appId=" + appId + "&wimaxProvider=" + provider+ "&wimaxPackageType=" + packageType+ "&drAccount=" + accNo;

    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(orderDetailsServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

  async wimaxRechargeHistory(token, userId, appId, fromDate, toDate) {
    let WimaxRechargeHistServiceURL = this.configLoader.getIBankWSUrl()+"services/wimaxPayServices/getWimaxRechargeHistory?";
    let paramValue = "access_token="+token+"&userId=" + userId + "&appId=" + appId +"&startDate=" + fromDate+ "&endDate=" + toDate;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(WimaxRechargeHistServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }
}



