import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {ConfigProvider} from "../config/config";
import {LoadingController} from "ionic-angular";

/*
  Generated class for the ChequeBookServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ChequeBookServiceProvider {

  constructor(public httpClient: HttpClient,
              public configLoader: ConfigProvider,
              public loadingCtrl: LoadingController) {
  }

  //FUNCTION FOR GET CHEQUE BOOL LEAF LIST
  async fetchChqBookLeafList(token, userId, appId, srAccountNo) {
    let fetchChqBookLeafListServiceURL = this.configLoader.getIBankWSUrl() + "services/chequeBookRequisition/getChqBookLeafList?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId + "&selectedAccount=" + srAccountNo;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(fetchChqBookLeafListServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

  //FUNCTION FOR VALIDATE TIPN
  async verifyTpinForChqBookRequisition(token, userId, appId, accNo, tpin) {
    let verifyTpinForChqBookRequisitionServiceURL = this.configLoader.getIBankWSUrl() + "services/chequeBookRequisition/verifyTpinForChqBookRequisition?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId + "&accNo=" + accNo+ "&tpin=" + tpin;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(verifyTpinForChqBookRequisitionServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

  //FUNCTION FOR ISSUE CHEQUE BOOK
  async issueChqBook(token, userId, appId, accNo, leafNo, otp) {
    let issueChqBookListServiceURL = this.configLoader.getIBankWSUrl() + "services/chequeBookRequisition/issueChqBookRequisition?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId + "&accNo=" + accNo+ "&leafNo=" + leafNo + "&otp=" + otp;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(issueChqBookListServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

  //FUNCTION FOR FETCH CHEQUE BOOK REQ HISTORY
  async chqBookReqHist(token, userId, appId, fromDate, toDate) {
    let chqBookReqHistServiceURL = this.configLoader.getIBankWSUrl() + "services/chequeBookRequisition/chqBookReqHist?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId + "&fromDate=" + fromDate+ "&toDate=" + toDate;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(chqBookReqHistServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

}



