import {Component, ViewChild} from '@angular/core';
import {AlertController, Events, Nav, Platform, ToastController} from 'ionic-angular';
import {StatusBar} from '@ionic-native/status-bar';
import {CryptoParamUtilProvider} from "../providers/crypto-param-util/crypto-param-util";
import {LocalStorageProvider} from "../providers/local-storage/local-storage";
import {ConfigProvider} from "../providers/config/config";
import {NetworkProvider} from "../providers/network/network";
import {Network} from "@ionic-native/network";
import {Diagnostic} from "@ionic-native/diagnostic";
import {DEFAULT_INTERRUPTSOURCES, Idle} from '@ng-idle/core';
import {timer} from 'rxjs/observable/timer';
import {SplashScreen} from "@ionic-native/splash-screen";


@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  showSplash = true;
  privMenus;
  appVersion;
  showedAlert: boolean;
  confirmAlert;
  rootPage: string = 'UserLogin01Page';

  pages: Array<{ title: string, component: any }>;

  constructor(public platform: Platform,
              public statusBar: StatusBar,
              public storage: LocalStorageProvider,
              public config: ConfigProvider,
              public cryptoUtil: CryptoParamUtilProvider,
              public alertCtrl: AlertController,
              public events: Events,
              public networkProvider: NetworkProvider,
              public network: Network,
              public diagnostic: Diagnostic,
              public toastCtrl: ToastController,
              public idle: Idle,
              // private googleAnalytics: GoogleAnalytics,
              private splashScreen: SplashScreen) {


    this.events.subscribe('mainMenu', (data) => {
      // console.log("Data: "+data);
      this.privMenus = JSON.parse(data);
    });
    this.initializeApp();

    this.appVersion = this.config.iSmartVersion;
  }


  initializeApp() {

    // this.rootPage = UserLogin01Page;
    this.platform.ready().then(() => {
      this.statusBar.styleDefault();
      this.splashScreen.hide();


      timer(3000).subscribe(() => this.showSplash = false);
      this.showedAlert = false;


      // this.googleAnalytics.startTrackerWithId("UA-76324512-1");
      // this.googleAnalytics.setAppVersion(this.config.iSmartVersion);
      // this.googleAnalytics.trackView("Google Analytics successfully initialized");

      //Google analytics
      // this.googleAnalytics.startTrackerWithId('UA-76324512-1', 30)
      //   .then(() => {
      //     console.log('Google analytics is ready now');
      //     this.googleAnalytics.setAppVersion("4.4.0");
      //     this.googleAnalytics.trackView('Login Page')
      //       .then(() => {
      //         console.log('Success');
      //       })
      //       .catch((e) => {
      //         console.log("Faild" + e)
      //       });
      //     this.googleAnalytics.debugMode();
      //     this.googleAnalytics.setAllowIDFACollection(true);
      //   })
      //   .catch(e => console.log('Error starting GoogleAnalytics', e));


      // Confirm exit
      this.platform.registerBackButtonAction(() => {

        if (this.nav.length() == 1) {
          if (!this.showedAlert) {
            this.confirmExitApp();
          }
          else {
            this.showedAlert = false;
            this.confirmAlert.dismiss();
          }
        }

        this.nav.pop();
      });

      // Network event when Offline
      this.networkProvider.initializeNetworkEvents();
      this.events.subscribe('network:offline', () => {
        // this.switchNetworkStatus();
        // alert('network:offline ==> '+this.network.type);
        let toast = this.toastCtrl.create({
          message: 'Your internet connection appears to be offline. Data integrity is not guaranteed.',
          duration: 7000,
          showCloseButton: true,
          closeButtonText: 'OK',
          dismissOnPageChange: true

        });
        toast.present();
      });

      if (this.network.type == "none") {
        this.switchNetworkStatus();
      }
      //idle.watch() start in user login 01 page
      this.idle.setIdle(10);
      this.idle.setTimeout(this.config.sessionExpireTimeInSecond);
      this.idle.setInterrupts(DEFAULT_INTERRUPTSOURCES);
      this.idle.onTimeout.subscribe(() => {
        this.nav.setRoot('ExpirePage');
        this.nav.popToRoot();
      });
    });
  }

  hideSplashScreen() {
    if (this.splashScreen) {
      setTimeout(() => {
        this.splashScreen.hide();
      }, 100);
    }
  }


  confirmExitApp() {
    this.showedAlert = true;
    this.confirmAlert = this.alertCtrl.create({
      title: "Confirm Exit",
      message: "<center>Are you sure you want to exit ?</center>",
      cssClass: "alertLogCss",
      enableBackdropDismiss: false,
      buttons: [
        {
          text: 'No',
          handler: () => {
            this.showedAlert = false;
            return;
          }
        },
        {
          text: 'Yes',
          handler: () => {
            this.platform.exitApp();
          }
        }
      ]
    });
    this.confirmAlert.present();
  }

  switchNetworkStatus() {

    let switchAlert = this.alertCtrl.create({
      title: "Network Disconnected !",
      message: "<center>The internet is disconnected on your device.</center>",
      cssClass: "alertLogCss",
      enableBackdropDismiss: false,
      buttons: [
        {
          text: 'Cancel',
          handler: () => {
            this.platform.exitApp();
          }
        },
        {
          text: 'Settings',
          handler: () => {
            this.diagnostic.switchToMobileDataSettings();
          }
        }
      ]
    });
    switchAlert.present();
  };

  openPage(page) {
    this.nav.setRoot(page.component);
  };

  checkMenuVersionReq = function (menuVersion) {
    let applicationVersion = this.appVersion.replace(/[.]/g, "");
    menuVersion = menuVersion.replace(/[.]/g, "");
    return (menuVersion <= applicationVersion);
  };


  pageSelectorAction = function (menuItem) {

    // this.nav.setRoot(page.component);
    let valueAfterRemHash = (menuItem.menuLink.replace("#", ""));
    let valueAfterReplaceSlash = (valueAfterRemHash.replace("iCellular/", ""));
    let allMenus;
    let dbVal = this.storage.retrieveFromStorage('privMenus');
    let pMenu = this.cryptoUtil.getDecryptVal(dbVal);
    if (pMenu == "") {
      allMenus = [];
    }
    else {
      allMenus = JSON.parse(pMenu);
    }

    let child = [];

    Object.keys(allMenus).forEach(key => {
      let value = allMenus[key];
      if (value.parentId == menuItem.menuId) {
        child.push(value);
      }
    });

    this.storage.insertIntoStorage("childDetails", JSON.stringify(child));
    this.storage.insertIntoStorage("menuText", JSON.stringify(menuItem.menuTxt));

    if (child.length <= 0) {
      this.nav.setRoot('WelcomePage', {});
      this.nav.push(valueAfterReplaceSlash, {});
    }

    else {
      this.nav.setRoot('WelcomePage', {});
      this.nav.push(valueAfterReplaceSlash, {});
    }
  };


  goToSetting = function () {
    this.nav.setRoot('WelcomePage');
    this.nav.push('SettingsPage', {});
  };

  goToContactUs = function () {
    this.nav.setRoot('WelcomePage');
    this.nav.push('ContactUsPage', {});
  };

  goToWelcomePage = function () {
    this.nav.setRoot('WelcomePage', {});
  };

  // CashByCodeAction = function () {
  //   this.nav.setRoot('WelcomePage');
  //   this.nav.push('CashByCode01Page', {});
  // };


  logout = function () {
    let dbVal = this.storage.retrieveFromStorage('appData');
    dbVal.refreshToken = "";
    dbVal.token = "";
    this.storage.insertIntoStorage('appData', dbVal);
    this.storage.removeFromIntoStorage('accounts');
    this.storage.removeFromIntoStorage('welcomeVal');
    this.storage.removeFromIntoStorage('passPolicy');

    this.nav.setRoot('UserLogin01Page');
    this.nav.popToRoot();
  };

}



