import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {ConfigProvider} from "../config/config";
import {LoadingController} from "ionic-angular";

/*
  Generated class for the AuthServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class AuthServiceProvider {

  constructor(public httpClient: HttpClient,
              public configLoader: ConfigProvider,
              public loadingCtrl: LoadingController) {

    // console.log('Hello AuthServiceProvider Provider');
  }

  async generateOTP(userId, appId, token) {
    let otpGenURL = this.configLoader.getIBankWSUrl() + "services/appEnroll/initAppEnroll?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(otpGenURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

  async validateOTP(userId, appId, token, otp, model, manufacturer) {
    let validateOTPUrl = this.configLoader.getIBankWSUrl() + "services/appEnroll/enqueueAppEnroll?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId + "&otp=" + otp + "&model=" + model + "&manufacturer=" + manufacturer;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(validateOTPUrl + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

  async validateActivation(userId, appId, token, activationCode) {
    let validateActURL = this.configLoader.getIBankWSUrl() + "services/appEnroll/checkAppEnroll?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId + "&activationCode=" + activationCode;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(validateActURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

  async resendEmail(userId, appId, token) {
    let resendEmailURL = this.configLoader.getIBankWSUrl() + "services/appEnroll/resendActivation?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(resendEmailURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }
}



