import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {ConfigProvider} from "../config/config";
import {LoadingController} from "ionic-angular";


@Injectable()
export class RechargeServiceProvider {

  constructor(public httpClient: HttpClient,
              public configLoader: ConfigProvider,
              public loadingCtrl: LoadingController) {
    // console.log('Hello RechargeServiceProvider Provider');
  }

  //FUNCTION FOR VALIDATE TRIGGER AMOUNT

  async validateRechargeRequest(token, userId, appId, amount) {
    let validateRechargeRequestServiceURL = this.configLoader.getIBankWSUrl()+"services/rechargeServices/validateRechargeRequest?";
    let paramValue = "access_token="+token+"&userId=" + userId + "&appId=" + appId + "&amount=" + amount;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(validateRechargeRequestServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

  //FUNCTION FOR MOBILE RECHARGE

  async makeMobileRecharge(token, userId, appId, accountNo, mobileNo, packageType, amount, tPin, otp, rechargeOP) {
    let rechargeServiceURL = this.configLoader.getIBankWSUrl()+"services/rechargeServices/requestRecharge?";
    let paramValue = "access_token="+token+"&userId=" + userId + "&appId=" + appId + "&drAccount=" + accountNo+ "&recipient=" + mobileNo+ "&packageType=" + packageType+ "&amount=" + amount+ "&tpin=" + tPin+ "&otp=" + otp+ "&operatorId=" + rechargeOP;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      // console.log(rechargeServiceURL + paramValue);
      this.httpClient.get(rechargeServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

  ////////////FUNCTION FOR FETCHING FAVORITE LIST
  async fetchFavoriteList(token, userId, appId) {
    let favoriteListServiceURL = this.configLoader.getIBankWSUrl()+"services/rechargeServices/getFavourites?";
    let paramValue = "access_token="+token+"&userId=" + userId + "&appId=" + appId;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(favoriteListServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status,
          statusCode: 404
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

  //FUNCTION FOR ADD TO FAVORITE LIST
  async addToFavoriteList(token, userId, appId,mobileNo, packageType, name) {
    let favoriteListServiceURL = this.configLoader.getIBankWSUrl()+"services/rechargeServices/addFavourite?";
    let paramValue = "access_token="+token+"&userId=" + userId + "&appId=" + appId + "&cellNo=" + mobileNo+ "&recipient=" + mobileNo+ "&packageType=" + packageType+ "&tag=" + name;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(favoriteListServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

  //FUNCTION FOR GENERATE iRECHARGE HISTORY
  async iRechargeHistory(token, userId, appId, fromDate, toDate) {
    let iRechargeHistoryServiceURL = this.configLoader.getIBankWSUrl()+"services/rechargeServices/getRechargeHistory?";
    let paramValue = "access_token="+token+"&userId=" + userId + "&appId=" + appId +"&startDate=" + fromDate+ "&endDate=" + toDate;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(iRechargeHistoryServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }
}



