import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {ConfigProvider} from "../config/config";
import {LoadingController} from "ionic-angular";

/*
  Generated class for the AccountServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class AccountServiceProvider {

  constructor(public httpClient: HttpClient,
              public configLoader: ConfigProvider,
              public loadingCtrl: LoadingController) {
    // console.log('Hello AccountServiceProvider Provider');
  }

  async getAccountInformation(token, userId, appId, accountNo) {
    let accountServiceURL = this.configLoader.getIBankWSUrl() + "services/accServices/accountInfo?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId + "&accountNo=" + accountNo;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(accountServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

  async getAccBalanceInformation(token, userId, appId, accountNo) {
    let accountBalanceServiceURL = this.configLoader.getIBankWSUrl() + "services/accServices/balanceEnquiry?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId + "&accountNo=" + accountNo;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(accountBalanceServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }


  async getAccountStatement(token, userId, appId, accountNo, fromDate, toDate, docType) {

    let accountStmntServiceURL = this.configLoader.getIBankWSUrl() + "services/accServices/accountStatement?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId + "&accountNo=" + accountNo + "&fromDate=" + fromDate + "&toDate=" + toDate + "&docType=" + docType;

    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(accountStmntServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

  async getTxSummary(token, userId, appId) {
    let txSummaryServiceURL = this.configLoader.getIBankWSUrl() + "services/accServices/accountTransSummary?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(txSummaryServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

  async getTxSummaryWithoutLoading(token, userId, appId) {
    let txSummaryServiceURL = this.configLoader.getIBankWSUrl() + "services/accServices/accountTransSummary?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId;

    return new Promise(resolve => {
      this.httpClient.get(txSummaryServiceURL + paramValue).subscribe(data => {
        resolve(data);
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
      });

    });
  }
}



