import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {ConfigProvider} from "../config/config";
import {MaskTokenProvider} from "../mask-token/mask-token";
import {LocalStorageProvider} from "../local-storage/local-storage";
import {LoadingController } from 'ionic-angular';
import 'rxjs/add/operator/timeout';


/*
  Generated class for the TokenServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class TokenServiceProvider {

  tokenUrl = "";
  paramValue = "";
  errorResponse;

    constructor(public httpClient: HttpClient,
              public configLoader: ConfigProvider,
              public localStorage: LocalStorageProvider,
              public loadingCtrl: LoadingController,
              public maskTokenUtil: MaskTokenProvider) {
  }


  async getRefreshToken(userId,PassWd) {
    PassWd = encodeURIComponent(PassWd);
    this.tokenUrl = this.configLoader.getIBankWSUrl() + "oauth/token?";
    this.paramValue = "client_id=ibankws-trusted-client&grant_type=password&username=" + userId + "&password=" + PassWd;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(this.tokenUrl+this.paramValue).timeout(20000).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        this.errorResponse = {
          message: err.statusText ,
          status: err.status
        };
        resolve(this.errorResponse);
        loading.dismiss();
      });

    });

  }



  ////////////////////////////GET NEW REFRESH TOKEN///////////////////////////////////////////

  async getNewToken(refreshToken) {
    let newTokenGenUrl = this.configLoader.getIBankWSUrl() + "oauth/token?";
    let newParamValue = "client_id=ibankws-trusted-client&grant_type=refresh_token&refresh_token=" + refreshToken;

    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(newTokenGenUrl+newParamValue).timeout(20000).subscribe(data => {
        // alert(JSON.stringify(data));
        resolve(data);

        //Setting new Value in storage
        this.updateDatabaseValue(data);
        loading.dismiss();
      }, err => {

        this.errorResponse = {
          message: err.statusText ,
          status:0 //Status 0 means token generation error. then it will forward to Session expire page
        };
        resolve(this.errorResponse);
        loading.dismiss();
      });

    });
  }

  updateDatabaseValue(data){
    let dbVal = this.localStorage.retrieveFromStorage('appData');
    dbVal.token = this.maskTokenUtil.getMaskVal(data.value);
    dbVal.refreshToken = this.maskTokenUtil.getMaskVal(data.refreshToken.value);
    this.localStorage.insertIntoStorage('appData',dbVal);
  }
}



