import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {LoadingController} from "ionic-angular";
import {ConfigProvider} from "../config/config";
import 'rxjs/add/operator/timeout';


@Injectable()
export class AuthenticationProvider {

  constructor(public httpClient: HttpClient,
              public configLoader: ConfigProvider,
              public loadingCtrl: LoadingController) {
  }


  async changePassWd(token, userId, appId, passWd) {
    let passWdServiceURL = this.configLoader.getIBankWSUrl() + "services/iCellAppAuth/updatePasswordExpiry?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId + "&newPasswd=" + passWd;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(passWdServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }


  async modifyOldPassWd(token, userId, appId, oldPassWd, newPassWd) {
    let passWdModifyServiceURL = this.configLoader.getIBankWSUrl() + "services/iCellAppAuth/changePassword?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId + "&oldpasswd=" + oldPassWd + "&newPasswd=" + newPassWd;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(passWdModifyServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

  async initChangePin(token, userId, appId) {
    let initChangePinServiceURL = this.configLoader.getIBankWSUrl() + "services/iCellAppAuth/initChangeTpin?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(initChangePinServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }


  async changeTpin(token, userId, appId, authCode,oldTpin, newTpin) {
    let changeTpinServiceURL = this.configLoader.getIBankWSUrl() + "services/iCellAppAuth/changeTpin?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId+ "&authCode=" + authCode+ "&oldTpin=" + oldTpin+ "&newTpin=" + newTpin;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(changeTpinServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

  ////////////FUNCTION FOR GET CAPTCHA IMAGE
  async getCaptcha() {
    let headers = new HttpHeaders();
    headers = headers.append("Authorization", "Basic " + btoa("10c@t10nS3rv1c3:10c@t10nS3rv1c3P@$$"));
    headers = headers.append("Content-Type", "application/x-www-form-urlencoded");

    let captchaUrl = this.configLoader.getResetPasswordBaseUrl() + "getCaptcha?";
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(captchaUrl, {headers: headers}).timeout(30000).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status,
          statusCode: 404
        };
        resolve(errorResponse);
        loading.dismiss();

      });
    });
  }

  ////////////Init reset password
  async initResetProfilePwdRequest(userId, accountNo, captchaId, captchaValue) {
    let headers = new HttpHeaders();
    headers = headers.append("Authorization", "Basic " + btoa("10c@t10nS3rv1c3:10c@t10nS3rv1c3P@$$"));
    headers = headers.append("Content-Type", "application/x-www-form-urlencoded");

    let paramValue = "userId=" + userId + "&accountNo=" + accountNo + "&captchaId=" + captchaId + "&captchaValue=" + captchaValue;

    let resetUrl = this.configLoader.getResetPasswordBaseUrl() + "initResetProfilePwdRequest?";
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(resetUrl + paramValue, {headers: headers}).timeout(30000).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status,
          statusCode: 404
        };
        resolve(errorResponse);
        loading.dismiss();

      });
    });
  }

  ////////////Confirm reset password
  async confirmResetProfilePwdRequest(resetCode, pwd, initToken) {
    let headers = new HttpHeaders();
    headers = headers.append("Authorization", "Basic " + btoa("10c@t10nS3rv1c3:10c@t10nS3rv1c3P@$$"));
    headers = headers.append("Content-Type", "application/x-www-form-urlencoded");

    let paramValue = "resetCode=" + resetCode + "&pwd=" + pwd + "&initToken=" + initToken;

    let resetUrl = this.configLoader.getResetPasswordBaseUrl() + "confirmResetProfilePwdRequest?";
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(resetUrl + paramValue, {headers: headers}).timeout(30000).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status,
          statusCode: 404
        };
        resolve(errorResponse);
        loading.dismiss();

      });
    });
  }

  ////////////Init reset password
  async checkProfileCurrentStatus(userId) {
    let headers = new HttpHeaders();
    headers = headers.append("Authorization", "Basic " + btoa("10c@t10nS3rv1c3:10c@t10nS3rv1c3P@$$"));
    headers = headers.append("Content-Type", "application/x-www-form-urlencoded");

    let paramValue = "userId=" + userId;

    let profileUrl = this.configLoader.getProfileCurrentStatusBaseUrl() + "initSignIn?";
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(profileUrl + paramValue, {headers: headers}).timeout(20000).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status,
          statusCode: 404
        };
        resolve(errorResponse);
        loading.dismiss();

      });
    });
  }


}



