import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import 'rxjs/add/observable/fromPromise';
import {Storage} from "@ionic/storage";

/*
  Generated class for the LocalStorageProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class LocalStorageProvider {

  public var;
  public keyVal: string = "";

  constructor(public http: HttpClient, public storage: Storage) {
    // console.log('Hello LocalStorageProvider Provider');
  }


  insertIntoStorage(keyName, data) {
    try {
      window.localStorage.setItem("ngStorage-" + keyName, JSON.stringify(data));
    }
    catch (e) {
      console.log(e);
    }
  }

  removeFromIntoStorage(keyName) {
    try {
      window.localStorage.removeItem("ngStorage-" + keyName);
    }
    catch (e) {
      console.log(e);

    }
  }

  retrieveFromStorage(key) {
    try {
      return JSON.parse(window.localStorage.getItem("ngStorage-" + key));
    }
    catch (e) {
      console.log(e);
      return null;
    }
  }

}



