import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {Diagnostic} from '@ionic-native/diagnostic';

@Injectable()
export class ConfigProvider {
  public iSmartVersion: string = "4.4.5";
  public gpsCheckEnabled : boolean= true;
  public internetCheckEnabled : boolean=true;
  public mapZoomingMultiple = 15;
  public mapZoomingSingle= 15;
  public noOfLocationNearestMe=10;
  public googleAnalyticTrackViewID="UA-76324512-1";
  public sessionExpireTimeInSecond=300;
  public receiverTypeCard="CRD";
  public receiverTypeAccount="ACC";
  public transferTypeNPSB="IBFT";


  constructor(public http: HttpClient, public diagnostic: Diagnostic) {
  }


  getIBankWSUrl = function () {
    return 'https://ibankws.islamibankbd.com/';
    // return 'https://10.190.43.90:8998/iBankWS/';
  };

  getProfileCurrentStatusBaseUrl = function () {
    return 'https://ibankws.islamibankbd.com/ibblLocation/services/';
    // return 'https://10.190.43.90:8998/iSmartMWare/services/';
  };

  getLocationBaseUrl = function () {
    return 'https://ibankws.islamibankbd.com/ibblLocation/services/';
    // return 'https://10.190.43.90:8998/ibblLocation/services/';

  };

  getResetPasswordBaseUrl = function () {
    return 'https://ibankws.islamibankbd.com/ibblLocation/services/';
    // return 'https://10.190.43.90:8998/ibblLocation/services/';
  };

  getRegistrationURL= function(){
    return 'https://ibblportal.islamibankbd.com/instrucRegistration.do';
  };


  getRemittanceBaseUrl = function () {
    return 'https://ibankws.islamibankbd.com/ibblLocation/services/';
    // return 'https://10.190.43.90:8998/iSmartMWare/services/';

  };

  getLoadingSpinner = function () {
    return 'bubbles';
  };

}



