import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {ConfigProvider} from "../config/config";
import {FlurryAnalytics, FlurryAnalyticsObject, FlurryAnalyticsOptions} from '@ionic-native/flurry-analytics';
import {LocalStorageProvider} from "../local-storage/local-storage";
import {CryptoParamUtilProvider} from "../crypto-param-util/crypto-param-util";


@Injectable()
export class GoogleAnalyticsProvider {
  options :FlurryAnalyticsOptions;

  constructor(public http: HttpClient,
              public config: ConfigProvider,
              public flurryAnalytics: FlurryAnalytics,
              public storage : LocalStorageProvider,
              public cryptoUtil: CryptoParamUtilProvider) {

    let appData = this.storage.retrieveFromStorage("appData");
    let userID=this.cryptoUtil.getDecryptVal(appData.userId);

    this.options= {
      appKey: 'V46D8CW4RWXSRDC5BBCJ', // REQUIRED
      reportSessionsOnClose: true,
      version: this.config.iSmartVersion,
      enableLogging: true,
      userId: userID
    }
  }

  // startTrackerWithId = function (viewName) {
  //   this.ga.startTrackerWithId('UA-76324512-1', 30)
  //     .then(() => {
  //
  //       this.ga.setAppVersion(this.config.iSmartVersion);
  //       this.ga.trackView(viewName)
  //         .then(() => {
  //           // console.log('Success');
  //         })
  //         .catch((e) => {
  //           // console.log("Faild" + e)
  //         });
  //
  //       this.ga.debugMode();
  //       this.ga.setAllowIDFACollection(true);
  //     })
  //     .catch(e => console.log('Error starting GoogleAnalytics', e));
  // };

  startTrackerWithId = function (viewName) {
    let fa: FlurryAnalyticsObject = this.flurryAnalytics.create(this.options);

    fa.logEvent(viewName)
      .then(() => console.log('Logged an event!'))
      .catch(e => console.log('Error logging the event', e));
  };
  // startTrackerWithId = function (viewName) {
  //   this.ga.trackView(viewName)
  //     .then(() => {
  //     })
  //     .catch(e => console.log(e));
  // }
}



