import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {ConfigProvider} from "../config/config";
import {LoadingController} from "ionic-angular";

/*
  Generated class for the UtilitybillpayProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class UtilitybillpayProvider {

  constructor(public httpClient: HttpClient,
              public configLoader: ConfigProvider,
              public loadingCtrl: LoadingController) {

  }
  async getUtilityBillCompanyList(token, userId, appId) {
    let companyListServiceURL = this.configLoader.getIBankWSUrl()+"services/utilityBillPayServices/getUtilityCompany?";
    let paramValue = "access_token="+token+"&userId=" + userId + "&appId=" + appId;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(companyListServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });
    });
  }

  async getCompanyAssociationLabels(token, userId, appId, companyCode, regType, companyCategory) {
    let associationLabelServiceURL = this.configLoader.getIBankWSUrl()+"services/utilityBillPayServices/getCompanyAssociationLabels?";
    let paramValue = "access_token="+token+"&userId=" + userId + "&appId=" + appId+"&companyCode=" + companyCode + "&regType=" + regType+ "&companyCategory=" + companyCategory;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(associationLabelServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });
    });
  }

  async makeCompanyAssociation(token, userId, appId, companyCode, regType, companyCategory,dynamicParams) {
    let associationLabelServiceURL = this.configLoader.getIBankWSUrl()+"services/utilityBillPayServices/utilityCompanyAssociation?";
    let paramValue = "access_token="+token+"&userId=" + userId + "&appId=" + appId+"&companyCode=" + companyCode + "&regType=" + regType+ "&companyCategory=" + companyCategory;
    paramValue=paramValue+dynamicParams;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(associationLabelServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });
    });
  }

  async getRegisteredAccList(token, userId, appId, companyCode) {
    let regAccListServiceURL = this.configLoader.getIBankWSUrl()+"services/utilityBillPayServices/getUtilityBillPayAccInfos?";
    let paramValue = "access_token="+token+"&userId=" + userId + "&appId=" + appId+"&companyCode=" + companyCode;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(regAccListServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });
    });
  }

  async getUtilityBillPayLabels(token, userId, appId, companyCode, debitAcc, companyCategory, isNewReg, billingAcc) {
    let utilityBillPayLabelsServiceURL = this.configLoader.getIBankWSUrl()+"services/utilityBillPayServices/getUtilityBillPayLabels?";
    let paramValue = "access_token="+token+"&userId=" + userId + "&appId=" + appId+"&companyCode=" + companyCode + "&debitAcc=" + debitAcc+ "&companyCategory=" + companyCategory+ "&isNewReg=" + isNewReg+ "&billingAcc=" + billingAcc;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(utilityBillPayLabelsServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });
    });
  }

  async enqueueUtilityBill(token, userId, appId, companyCode, debitAcc, dynamicParams, isNewReg, tpin) {
    let enqueueUtilityBillPayServiceURL = this.configLoader.getIBankWSUrl()+"services/utilityBillPayServices/enqueueUtilityBillPay?";
    let paramValue = "access_token="+token+"&userId=" + userId + "&appId=" + appId+"&companyCode=" + companyCode + "&debitAcc=" + debitAcc+  "&data=" + dynamicParams+"&isNewReg=" + isNewReg+"&tpin=" + tpin;

    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(enqueueUtilityBillPayServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });
    });
  }

  async PayUtilityBill(token, userId, appId, utilityServiceId, tpin, otp, isNewReg, companyData) {
    let PayUtilityBillServiceURL = this.configLoader.getIBankWSUrl()+"services/utilityBillPayServices/utilityBillPay?";
    let paramValue = "access_token="+token+"&userId=" + userId + "&appId=" + appId+"&utilityServiceId=" + utilityServiceId + "&tpin=" + tpin+ "&otp=" + otp+"&isNewReg=" + isNewReg+"&companyBillData=" + companyData;

    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(PayUtilityBillServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });
    });
  }

  //FUNCTION FOR GENERATE iRECHARGE HISTORY
  async utilityBillPaymentHistory(token, userId, appId, fromDate, toDate) {
    let iRechargeHistoryServiceURL = this.configLoader.getIBankWSUrl()+"services/utilityBillPayServices/getUtilityBillPayHistory?";
    let paramValue = "access_token="+token+"&userId=" + userId + "&appId=" + appId +"&startDate=" + fromDate+ "&endDate=" + toDate;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(iRechargeHistoryServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

  //FUNCTION FOR GENERATE iRECHARGE HISTORY
  async fetchCaptcha(token, userId, appId) {
    let iRechargeHistoryServiceURL = this.configLoader.getIBankWSUrl()+"services/captchaGenerator/generateCaptcha?";
    let paramValue = "access_token="+token+"&userId=" + userId + "&appId=" + appId;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(iRechargeHistoryServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

}



