import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Injectable } from '@angular/core';
import {LoadingController} from "ionic-angular";
import {ConfigProvider} from "../config/config";
import 'rxjs/add/operator/timeout';

/*
  Generated class for the LocationProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class LocationProvider {

  constructor(public httpClient: HttpClient,
              public configLoader: ConfigProvider,
              public loadingCtrl: LoadingController) {
    // console.log('Hello LocationProvider Provider');
  }

  ////////////FUNCTION FOR FETCHING FAVORITE LIST
  async getActiveLocationByType(locationType) {
    let headers = new HttpHeaders();
    headers = headers.append("Authorization", "Basic " + btoa("10c@t10nS3rv1c3:10c@t10nS3rv1c3P@$$"));
    headers = headers.append("Content-Type", "application/x-www-form-urlencoded");
    // let encodedString = btoa("10c@t10nS3rv1c3:10c@t10nS3rv1c3P@$$");
    let locationURL = this.configLoader.getLocationBaseUrl()+"getActiveLocationByType?";
    let paramValue = "locationType=" + locationType;
    // console.log(locationURL+paramValue);
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(locationURL + paramValue,{headers : headers}).timeout(35000).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {


        let errorResponse = {
          message: err.statusText,
          status: err.status,
          statusCode: 404
        };
        resolve(errorResponse);
        loading.dismiss();

      });

    });
  }

  getLocationDescription = function (loc) {
    if (loc == 1) {
      return "Branch";
    }
    else if (loc == 2) {
      return "ATM";
    }
    else if (loc == 3) {
      return "Agent";
    }
    else {
      return "";
    }
  };

  getDistanceFromLatLonInKm(lat1, lon1, lat2, lon2) {
    let R = 6371;
    let dLat = this.deg2rad(lat2 - lat1);
    let dLon = this.deg2rad(lon2 - lon1);
    let a =
      Math.sin(dLat / 2) * Math.sin(dLat / 2) +
      Math.cos(this.deg2rad(lat1)) * Math.cos(this.deg2rad(lat2)) *
      Math.sin(dLon / 2) * Math.sin(dLon / 2)
    ;
    let c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    let d = R * c; // Distance in km
    return Number(d).toFixed(3);
    // return d;
  }

  deg2rad(deg) {
    return deg * (Math.PI / 180)
  }
}



