import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

/*
  Generated class for the MaskTokenProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class MaskTokenProvider {

  constructor(public http: HttpClient) {
    // console.log('Hello MaskTokenProvider Provider');
  }

  getMaskVal = function (plainText) {
    let length=plainText.length;
    let maskValue=plainText.substring(length-4,length)+plainText.substr(0,length-4);
    //$log.info("Value Before Masking : "+plainText);
    //$log.info("Value After Masking : "+maskValue);
    return maskValue;
  };

  getUnMaskVal = function (maskValue) {
    let length=maskValue.length;
    let plainText=maskValue.substring(4,length)+maskValue.substr(0,4);
    //$log.info("Value Before UnMasking : "+maskValue);
    //$log.info("Value After UnMasking : "+plainText);
    return plainText;
  };

  getDeviceID = function () {
    let date = new Date();
    let uuid = Math.floor(Math.random() * 900000000000) + 100000000000;
    let millSec = date.getMilliseconds();
    //Date format was 'yyyyMMddHHmmss'
    let today = date.getFullYear().toString() + (date.getMonth() + 1).toString() +
      date.getDate().toString() + date.getHours().toString() + date.getMinutes().toString() +
      date.getMinutes().toString();

    return uuid.toString()+ today + millSec;

  };


}



