import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import CryptoJS from 'crypto-js';

/*
  Generated class for the CryptoParamUtilProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class CryptoParamUtilProvider {

  password = "1B@nk1ngS3cr3tK$";
  salt = "3FF2EC019C627B945225DEBAD71A01B6985FE84C95A70EB132882F88C0A59A55";
  iv = "12345678901234567890123456789012";
  key = CryptoJS.PBKDF2(this.password, CryptoJS.enc.Hex.parse(this.salt), {keySize: 128 / 32, iterations: 10});

  constructor(public http: HttpClient) {
    // console.log('Hello CryptoParamUtilProvider Provider');
  }

  getEncryptVal = function (plainText) {
    let encryptValue = CryptoJS.AES.encrypt(plainText, this.key, {iv: CryptoJS.enc.Hex.parse(this.iv)}).ciphertext.toString(CryptoJS.enc.Base64);
    //$log.info("Value After Encrypt : "+encryptValue);
    encryptValue = encodeURIComponent(encryptValue);
    //$log.info("Value After Encode : "+encryptValue);
    return encryptValue;
  };

  getDecryptVal = function (encValue) {

    //$log.info("Encode value with URI"+encValue);
    let decodeUri = decodeURIComponent(encValue);

    //$log.info("Encode value without URI"+decodeUri);
    let decValue = CryptoJS.AES.decrypt(decodeUri, this.key, {iv: CryptoJS.enc.Hex.parse(this.iv)});

    //$log.info("Value After Decrypt : "+decValue);
    let uftVal = CryptoJS.enc.Utf8.stringify(decValue)

    //$log.info("Value After UFT : "+uftVal);
    return uftVal;
  };

  getSha256EncryptedValue = function (plainText) {
    return CryptoJS.SHA256(plainText);
  }

}



