import {BrowserModule} from '@angular/platform-browser';
import {ErrorHandler, NgModule} from '@angular/core';
import {AlertController, IonicApp, IonicErrorHandler, IonicModule} from 'ionic-angular';
import {IonicStorageModule} from '@ionic/storage';

import {MyApp} from './app.component';

import {StatusBar} from '@ionic-native/status-bar';
import {Device} from '@ionic-native/device';
import {AppVersion} from "@ionic-native/app-version";
import {CryptoParamUtilProvider} from '../providers/crypto-param-util/crypto-param-util';
import {HttpClientModule} from "@angular/common/http";
import {LocalStorageProvider} from '../providers/local-storage/local-storage';
import {ConfigProvider} from '../providers/config/config';
import {MaskTokenProvider} from '../providers/mask-token/mask-token';
import {FormsModule, ReactiveFormsModule} from "@angular/forms";
import {TokenServiceProvider} from '../providers/token-service/token-service';
import {ErrorMessageProvider} from '../providers/error-message/error-message';
import {AuthServiceProvider} from '../providers/auth-service/auth-service';
import {MenuServiceProvider} from '../providers/menu-service/menu-service';
import {RechargeServiceProvider} from '../providers/recharge-service/recharge-service';
import {DatePickerModule} from 'ion-datepicker';
import {DatePicker} from "@ionic-native/date-picker";
import {AccountServiceProvider} from '../providers/account-service/account-service';
import {WimaxServiceProvider} from '../providers/wimax-service/wimax-service';
import {ITransferServiceProvider} from '../providers/i-transfer-service/i-transfer-service';
import {AuthenticationProvider} from '../providers/authentication/authentication';
import {Diagnostic} from "@ionic-native/diagnostic";
import {LocationProvider} from '../providers/location/location';
import {AlertProvider} from '../providers/alert/alert';

import {GoogleMaps} from "@ionic-native/google-maps";
import {Geolocation} from "@ionic-native/geolocation";
import {InAppBrowser} from "@ionic-native/in-app-browser";
import {NetworkProvider} from '../providers/network/network';
import {Network} from "@ionic-native/network";
import {Idle} from "@ng-idle/core";
import {NgIdleKeepaliveModule} from "@ng-idle/keepalive";
import {HttpModule} from "@angular/http";
import {GoogleAnalytics} from "@ionic-native/google-analytics";
import {GoogleAnalyticsProvider} from '../providers/google-analytics/google-analytics';
import {FundTransferOtherBankProvider} from '../providers/fund-transfer-other-bank/fund-transfer-other-bank';
import {Contacts} from "@ionic-native/contacts";
import {ChequeBookServiceProvider} from '../providers/cheque-book-service/cheque-book-service';
import {SplashScreen} from "@ionic-native/splash-screen";
import {RemittanceProvider} from '../providers/remittance/remittance';
import {UtilitybillpayProvider} from '../providers/utilitybillpay/utilitybillpay';
import {FlurryAnalytics} from '@ionic-native/flurry-analytics'
import { DocumentViewer} from '@ionic-native/document-viewer';
import {File} from "@ionic-native/file";
import {FileOpener} from "@ionic-native/file-opener/";
import {FileTransfer} from "@ionic-native/file-transfer";
import { CashByCodeServiceProvider } from '../providers/cash-by-code-service/cash-by-code-service';


@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp,{
      backButtonText: '',
      backButtonIcon : 'ios-arrow-back',
      modalEnter: 'modal-slide-in',
      modalLeave: 'modal-slide-out',
      pageTransition: 'ios-transition',
      // animate : false
    }),
    IonicStorageModule.forRoot(),
    HttpClientModule,
    FormsModule,
    ReactiveFormsModule,
    DatePickerModule,
    NgIdleKeepaliveModule.forRoot(),
    HttpModule,

  ],
  bootstrap: [IonicApp],

  entryComponents: [
    MyApp
  ],

  providers: [
    StatusBar,
    SplashScreen,
    Device,
    AppVersion,
    AlertController,
    Diagnostic,
    GoogleMaps,
    GoogleAnalytics,
    FlurryAnalytics,
    Geolocation,
    CryptoParamUtilProvider,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    LocalStorageProvider,
    ConfigProvider,
    MaskTokenProvider,
    TokenServiceProvider,
    ErrorMessageProvider,
    AuthServiceProvider,
    MenuServiceProvider,
    RechargeServiceProvider,
    DatePicker,
    AccountServiceProvider,
    WimaxServiceProvider,
    ITransferServiceProvider,
    AuthenticationProvider,
    LocationProvider,
    AlertProvider,
    InAppBrowser,
    Network,
    NetworkProvider,
    Idle,
    GoogleAnalyticsProvider,
    FundTransferOtherBankProvider,
    Contacts,
    ChequeBookServiceProvider,
    RemittanceProvider,
    UtilitybillpayProvider,
    File,
    FileOpener,
    FileTransfer,
    DocumentViewer,
    CashByCodeServiceProvider
  ]
})
export class AppModule {}



