import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {ConfigProvider} from "../config/config";
import {LoadingController} from "ionic-angular";

/*
  Generated class for the ITransferServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ITransferServiceProvider {

  constructor(public httpClient: HttpClient,
              public configLoader: ConfigProvider,
              public loadingCtrl: LoadingController) {
    // console.log('Hello ITransferServiceProvider Provider');
  }

  //FUNCTION FOR GET SOURCE  DESTINATION AND PURPOSE LIST
  async fetchDestAndPurposeList(token, userId, appId, srAccountNo) {
    let fetchDestAndPurposeListServiceURL = this.configLoader.getIBankWSUrl() + "services/localFundTransfer/getDestAccList?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId + "&sourceAccount=" + srAccountNo;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(fetchDestAndPurposeListServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }

  //FUNCTION FOR GET SOURCE  DESTINATION AND PURPOSE LIST
  async fetchCreditAccountList(token, userId, appId, srAccountNo) {
    let fetchCreditAccountServiceURL = this.configLoader.getIBankWSUrl() + "services/localFundTransfer/getCreditAccountList?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId + "&sourceAccount=" + srAccountNo;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(fetchCreditAccountServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }


  //FUNCTION FOR INIT FUND TRANSFER
  async initFundTransfer(token, userId, appId, sourceAccount, destAccount, amount, note, purpose) {
    let initFundTransferServiceURL = this.configLoader.getIBankWSUrl() + "services/localFundTransfer/initFundTransfer?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId +
      "&sourceAccount=" + sourceAccount + "&destAccount=" + destAccount + "&amount=" + amount + "&note=" + note + "&purpose=" + purpose;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(initFundTransferServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }


  //FUNCTION FOR GENERATE OTP FOR FUND TRANSFER
  async genOtpForFundTransfer(token, userId, appId, transferId) {
    let genOtpForFundTransferServiceURL = this.configLoader.getIBankWSUrl() + "services/localFundTransfer/localTransfer2FactorAuth?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId + "&transferId=" + transferId;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(genOtpForFundTransferServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }


  //FUNCTION FOR GENERATE OTP FOR FUND TRANSFER
  async confirmFundTransfer(token, userId, appId, transferId, tpin, otp) {
    let confirmFundTransferServiceURL = this.configLoader.getIBankWSUrl() + "services/localFundTransfer/confirmLocalFundTransfer?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId + "&transferId=" + transferId + "&tpin=" + tpin + "&otp=" + otp;

    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(confirmFundTransferServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }


  //FUNCTION FOR GENERATE iTRANSFER HISTORY
  async iTransferHistory(token, userId, appId, fromDate, toDate) {
    let iTransferHistoryServiceURL = this.configLoader.getIBankWSUrl() + "services/localFundTransfer/getLocalTransferRecords?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId + "&startDate=" + fromDate + "&endDate=" + toDate;

    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(iTransferHistoryServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }


  //FUNCTION FOR ADD CREDIT ACCOUNT
  async addCreditAccount(token, userId, appId, crAccount, is2Factor) {
    let addCreditAccountServiceURL = this.configLoader.getIBankWSUrl() + "services/localFundTransfer/addCreditAccount?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId + "&crAccount=" + crAccount+ "&is2Factor=" + is2Factor;

    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(addCreditAccountServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }


  //FUNCTION FOR CONFIRM ADD CREDIT ACCOUNT
  async addCreditAccountConfirm(token, userId, appId, crAccount, otp) {
    let addCreditAccountConfirmServiceURL = this.configLoader.getIBankWSUrl() + "services/localFundTransfer/confirmAddCreditAccount?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId + "&crAccount=" + crAccount + "&verificationCode=" + otp;

    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(addCreditAccountConfirmServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }


  //FUNCTION FOR DELETE CREDIT ACCOUNT
  async deleteCreditAccount(token, userId, appId, crAccount) {
    let deleteCreditAccountServiceURL = this.configLoader.getIBankWSUrl() + "services/localFundTransfer/removeCreditAccountSubscription?";
    let paramValue = "access_token=" + token + "&userId=" + userId + "&appId=" + appId + "&creditAccountNo=" + crAccount;

    let loading = this.loadingCtrl.create({
      content: 'Please wait...',
      spinner: this.configLoader.getLoadingSpinner()
    });

    return new Promise(resolve => {
      loading.present();
      this.httpClient.get(deleteCreditAccountServiceURL + paramValue).subscribe(data => {
        resolve(data);
        loading.dismiss();
      }, err => {

        let errorResponse = {
          message: err.statusText,
          status: err.status
        };
        resolve(errorResponse);
        loading.dismiss();
      });

    });
  }
}



